import { createCustomElement, actionTypes } from '@servicenow/ui-core';
const { COMPONENT_RENDERED } = actionTypes;
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-empty-state';
import { uxfViewportBehavior } from '@devsnc/library-uxf';

const VIEWPORT_ROUTE_REQUESTED = 'VIEWPORT_ROUTE_REQUESTED';
const VIEWPORT_ROUTE_DISMISSED = 'VIEWPORT_ROUTE_DISMISSED';
const LOAD_VIEWPORT_CONTENT = 'LOAD_VIEWPORT_CONTENT';
const HOIST_VIEWPORT_CONTENT = 'HOIST_VIEWPORT_CONTENT';

const TAG_NAME = 'now-uxf-modal-viewport';
createCustomElement(TAG_NAME, {
	renderer: {
		type: snabbdom
	},
	view: () => <slot name="viewport" />,
	behaviors: [uxfViewportBehavior],
	actionHandlers: {
		[COMPONENT_RENDERED]: ({ host, action, dispatch, properties }) => {
			const { previousRenderProperties: prevProps } = action.payload;
			const { contents, activeRoute } = properties;

			if (contents && contents.route && contents !== prevProps.contents) {
				dispatch(LOAD_VIEWPORT_CONTENT, { contents });
			}

			if (activeRoute && activeRoute !== prevProps.activeRoute) {
				const elementRef = host.nowRootNode
					.querySelector('slot')
					.assignedElements()[0];
				dispatch(HOIST_VIEWPORT_CONTENT, { elementRef });
			}
		},
		[LOAD_VIEWPORT_CONTENT]: {
			modifier: { name: 'debounce', delay: 10 },
			stopPropagation: true,
			effect: ({ action, dispatch }) => {
				const {
					payload: { contents = {} }
				} = action;
				if (contents.route) {
					dispatch(VIEWPORT_ROUTE_REQUESTED, {
						...contents
					});
				}
			}
		},
		[HOIST_VIEWPORT_CONTENT]: {
			stopPropagation: true,
			effect: ({ action, dispatch, properties }) => {
				const {
					payload: { elementRef }
				} = action;

				const {
					activeRoute,
					displayOptions = {},
					bare,
					size,
					disableDismiss
				} = properties;

				if (!elementRef) {
					return;
				}

				// When this viewport is hoisted to the modal, it needs to use the
				// default slot rather than the `slot="viewport"` that UXF uses
				elementRef.setAttribute('slot', '');

				// Watch for when the viewport contents is no longer hoisted. This
				// happens when the user dismisses the modal, and at that point we need
				// to tell UXF that this screen has been dismissed
				const observer = new MutationObserver(mutations => {
					for (const mutation of mutations) {
						if (!mutation.target.nowHoist) {
							observer.disconnect();
							dispatch(VIEWPORT_ROUTE_DISMISSED, {
								route: activeRoute.routeType,
								fields: activeRoute.fields
							});
							break;
						}
					}
				});
				observer.observe(elementRef, {
					attributes: true,
					attributeFilter: ['now-hoist']
				});

				const payload = {
					modalId: elementRef.getAttribute('now-id'),
					displayOptions: { size, disableDismiss, ...displayOptions } || {},
					bare: Boolean(bare)
				};

				// This should be removed once we can verify that event mappings for this works.
				dispatch(
					'MODAL_SELECTED',
					{
						...payload,
						showModal: true
					},
					{ elementRef }
				);
				dispatch('NOW_UXF_VIEWPORT_MODAL#MODAL_CONTENT_LOADED', payload);
			}
		}
	},
	properties: {
		contents: {},
		displayOptions: {},
		size: {
			default: 'md'
		},
		disableDismiss: {
			default: false
		},
		bare: {
			default: false,
			schema: {
				type: 'boolean'
			}
		}
	}
});

export default TAG_NAME;
