import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import '@servicenow/now-collapse';
import '@servicenow/now-heading';
import '@servicenow/now-icon';
import '@servicenow/now-tooltip';
import { isEmpty, toString } from 'lodash';
import styles from './styles.scss';
import slottedPolyfillBehavior from '../slotted-polyfill-behavior';
import '@servicenow/now-empty-state';

const renderTooltip = (state, canCollapse, collapsed) => {
	const {
		properties: { expandButtonTooltipContent, collapseButtonTooltipContent }
	} = state;
	const tooltipMessage = collapsed
		? expandButtonTooltipContent
		: collapseButtonTooltipContent;
	return canCollapse ? (
		<now-tooltip
			id={`tooltip-${state.componentId}`}
			target-ref={state.targetRef}
			content={tooltipMessage}
			position={['top-start']}
		/>
	) : null;
};

const view = (state, { updateProperties, updateState }) => {
	const {
		properties: {
			label,
			bare,
			type,
			bgvar,
			border,
			canCollapse,
			collapsed,
			expandButtonTooltipContent,
			collapseButtonTooltipContent
		}
	} = state;

	const setTargetRef = ref => setTimeout(() => updateState({ targetRef: ref }));

	let bg;
	if (!canCollapse) {
		if (bgvar) bg = `variant-${bgvar}`;
		else if (type == 'visualizations') {
			bg = 'variant-neutral-1';
		}
	}

	let bordered;
	if (border && !canCollapse) bordered = true;

	return (
		<div
			class={{
				container: true,
				padded: !bare,
				bordered,
				[bg]: bg,
				collapsible: canCollapse,
				collapsed: collapsed
			}}
		>
			{!canCollapse ? null : (
				<button
					className="collapse-trigger"
					aria-expanded={!collapsed ? 'true' : 'false'}
					on-click={() => updateProperties({ collapsed: !collapsed })}
					hook-insert={({ elm }) => setTargetRef(elm)}
					hook-destroy={() => setTargetRef(null)}
					aria-describedby={`tooltip-${state.componentId}`}
					aria-label={
						collapsed
							? expandButtonTooltipContent
							: collapseButtonTooltipContent
					}
				>
					<now-icon
						icon={collapsed ? 'chevron-down-fill' : 'chevron-up-fill'}
						size="md"
					></now-icon>
					{isEmpty(label) ? null : (
						<now-heading
							label={toString(label)}
							level="2"
							variant="title-secondary"
							wontWrap={true}
						/>
					)}
					<hr />
				</button>
			)}
			<now-collapse expanded={!canCollapse || !collapsed}>
				{isEmpty(label) ? null : (
					<now-heading
						className="main-heading"
						label={toString(label)}
						level="2"
						variant="header-secondary"
						wontWrap={true}
					/>
				)}
				<slot>
					<now-empty-state variant="auto" />
				</slot>
			</now-collapse>
			{renderTooltip(state, canCollapse, collapsed)}
		</div>
	);
};

const TAG_NAME = 'now-uxf-page-container';
createCustomElement(TAG_NAME, {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		label: {},
		bare: {
			default: false,
			schema: {
				type: 'boolean'
			}
		},
		type: {},
		bgvar: {},
		border: {
			default: false,
			schema: {
				type: 'boolean'
			}
		},
		collapsed: {
			default: false,
			reflect: true,
			schema: {
				type: 'boolean'
			}
		},
		canCollapse: {
			default: false,
			reflect: true,
			schema: {
				type: 'boolean'
			}
		},
		expandButtonTooltipContent: {
			default: t('Expand container to show contents'),
			schema: {
				type: 'string'
			}
		},
		collapseButtonTooltipContent: {
			default: t('Collapse container to hide contents'),
			schema: {
				type: 'string'
			}
		}
	},
	styles
});

export default TAG_NAME;
