import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import focusBehavior from '@servicenow/behavior-focus';
import { layerEvents } from '@servicenow/library-enhanced-utils';
import '@servicenow/now-button';
import { t } from 'sn-translate';
import styles from './styles.scss';

const { COMPONENT_RENDERED } = actionTypes;
const DISMISS_ACTION = 'NOW_UXF_BARE_MODAL#DISMISSED';

const view = (state, { dispatch }) => {
	const {
		size,
		opened,
		disableDismiss,
		overlayPositionAbsolute
	} = state.properties;

	if (!opened) {
		return;
	}

	return (
		<div
			class={{
				'now-uxf-bare-modal-overlay': true,
				'now-uxf-bare-modal-absolute-overlay': overlayPositionAbsolute
			}}
		>
			<div
				role="dialog"
				class={{
					'now-uxf-bare-modal': true,
					['-' + size]: size
				}}
			>
				<div className="now-uxf-bare-modal-body">
					{disableDismiss ? (
						undefined
					) : (
						<now-button-iconic
							className="now-uxf-bare-modal-dismiss"
							icon="close-fill"
							variant="tertiary"
							size="md"
							bare
							config-aria={{ 'aria-label': t('Close dialog') }}
							tooltip-content={t('Close dialog')}
							on-click={() =>
								dispatch(DISMISS_ACTION, { reason: 'dismiss-icon' })
							}
						/>
					)}
					<slot className="now-uxf-bare-modal-slot" />
				</div>
			</div>
		</div>
	);
};

/**
 * A modal is a container that displays over application content to give
 * critical information or to ask users to complete a task in the middle of a
 * workflow. When the modal opens, users cannot access the application.
 *
 * Note: Set `aria-hidden="true"` on all non-modal elements when now-modal is
 * open to provide the correct experience for screen reader users.
 *
 * ```jsx
 * <now-modal
 * 	opened={openedApp}
 * 	size='sm'
 * 	header-label='Hello!'
 * 	content='And this is the body of the modal.'
 * 	footer-actions='[
 * 		{
 * 			"variant": "primary",
 * 			"label": "Save"
 * 		}
 * 	]'
 * />
 * ```
 *
 * @seismicElement now-modal
 * @summary A popup component that temporarily blocks underlying content to provide information or ask the user for a decision.
 */
createCustomElement('now-uxf-bare-modal', {
	properties: {
		/**
		 * Sets the width of the modal container. If "fullscreen", the container
		 * will fill to fit the screen. If "content", the width will shrink to fit
		 * the content.
		 * @type {('sm'|'md'|'lg'|'fullscreen'|'content')}
		 */
		size: {
			schema: {
				type: 'string',
				enum: ['sm', 'md', 'lg', 'fullscreen', 'auto']
			}
		},
		/**
		 * When true, the modal is open.
		 * @type {boolean}
		 */
		opened: {
			default: false,
			schema: { type: 'boolean' }
		},
		/**
		 * When true, the dismiss icon and Escape keydown handler are omitted. In
		 * this case, consuming components must provide some mechanism to close the
		 * modal.
		 * @type {boolean}
		 */
		disableDismiss: {
			default: false,
			schema: { type: 'boolean' }
		},
		/**
		 * When true, the position fixed of the overlay will be changed to absolute position.
		 * This helps the user to customize or render modal however they want.
		 * @type {boolean}
		 */
		overlayPositionAbsolute: {
			default: false,
			schema: { type: 'boolean' }
		}
	},
	view,
	behaviors: [
		{
			behavior: focusBehavior,
			options: { trap: true }
		}
	],
	styles,
	actions: {
		[DISMISS_ACTION]: {
			schema: {
				type: 'object',
				properties: {
					reason: {
						type: 'string',
						enum: ['dismiss-icon', 'keyboard-escape']
					}
				}
			}
		}
	},
	actionHandlers: {
		[COMPONENT_RENDERED]({ action, properties, dispatch }) {
			const { previousRenderProperties: prevProps } = action.payload;
			const { opened, disableDismiss, nowId: id } = properties;
			const isOpening = opened && !prevProps.opened && !disableDismiss;
			const isClosing = !opened && prevProps.opened;
			const isBecomingDismissable =
				!disableDismiss && prevProps.disableDismiss && opened;
			const isBecomingNonDismissable =
				disableDismiss && !prevProps.disableDismiss;

			if (isOpening || isBecomingDismissable) {
				layerEvents.add({
					id,
					events: ['keydown'],
					callback: event => {
						if (event.key !== 'Escape') return;

						dispatch(DISMISS_ACTION, { reason: 'keyboard-escape' });
					}
				});
			} else if (isClosing || isBecomingNonDismissable) {
				layerEvents.remove(id);
			}
		},
		[DISMISS_ACTION]({ updateProperties }) {
			updateProperties({
				opened: false
			});
		}
	}
});
