import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { nowLinkInterceptorBehavior } from '@devsnc/uxf-shared-behaviors';
import '@servicenow/now-alert';
import styles from './styles.scss';

const getHtmlArrayFromProps = props => {
	const { notifications = [] } = props;
	return notifications
		.filter(n => {
			const {
				content: { value = '' } = {},
				textLinkProps: { href = '' } = {}
			} = n;
			return !!n.content || value !== '' || href !== '';
		})
		.map(n => {
			const { content: { value } = {}, textLinkProps: { href } = {} } = n;
			let text = n.content;
			if (value) text = value;
			if (href) text = `<a href="${href}" />`;
			return text;
		});
};

const view = state => {
	const {
		properties: { notifications }
	} = state;
	if (!notifications || notifications.length === 0) return null;
	return <now-alert-list items={notifications} manage-items />;
};

const TAG_NAME = 'sn-uxf-page-notifications';
createCustomElement(TAG_NAME, {
	renderer: {
		type: snabbdom,
		view
	},
	behaviors: [
		{
			behavior: nowLinkInterceptorBehavior,
			options: {
				propsToHtmlArray: getHtmlArrayFromProps
			}
		}
	],
	properties: {
		notifications: { default: [] }
	},
	actionHandlers: {
		PREVIEW_RECORD: {
			effect: ({ action, dispatch }) => {
				dispatch('PAGE_NOTIFICATIONS#RECORD_LINK_CLICKED', {
					...action.payload
				});
			},
			stopPropagation: true
		}
	},
	styles
});

export default TAG_NAME;
