import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { isEmpty, toString, uniqueId, uniqBy, intersectionWith } from 'lodash';
import slottedPolyfillBehavior from '../slotted-polyfill-behavior';
import '../uxf-page-notifications/uxfPageNotifications';
import '@servicenow/now-alert';
import '@servicenow/now-heading';
import '@servicenow/now-empty-state';
import '@servicenow/now-loader';
import '@servicenow/now-progress-bar';
import styles from './styles.scss';

const actions = {
	NOTIFICATIONS_UPDATED: 'NOW_UXF_PAGE#NOTIFICATIONS_UPDATED',
	NOTIFICATION_ACTION_CLICKED: 'NOW_UXF_PAGE#NOTIFICATION_ACTION_CLICKED',
	LOADER_ACTION_CLICKED: 'NOW_UXF_PAGE#LOADER_ACTION_CLICKED',
	RECORD_LINK_CLICKED: 'NOW_UXF_PAGE#RECORD_LINK_CLICKED',
	LOADING_STATUS_CHANGED: 'NOW_UXF_PAGE#LOADING_STATUS_CHANGED',
	SET_PROPAGATED_NOTIFICATIONS: 'NOW_UXF_PAGE#SET_PROPAGATED_NOTIFICATIONS'
};
const handlers = {
	ADD_NOTIFICATIONS: 'NOW_UXF_PAGE#ADD_NOTIFICATIONS',
	REMOVE_NOTIFICATIONS: 'NOW_UXF_PAGE#REMOVE_NOTIFICATIONS',
	CLEAR_NOTIFICATIONS: 'NOW_UXF_PAGE#CLEAR_NOTIFICATIONS',
	SET_LOADING_STATE: 'NOW_UXF_PAGE#SET_LOADING_STATE'
};

const sortNotifications = notifications => {
	const NOTIFICATION_SORT_ORDER = {
		critical: 4,
		high: 3,
		warning: 2,
		info: 1,
		positive: 0
	};
	return notifications.sort(
		(a, b) =>
			NOTIFICATION_SORT_ORDER[a.status] - NOTIFICATION_SORT_ORDER[b.status]
	);
};

const transformedNotification = (id, type, message) => {
	const getIcon = type => {
		switch (type) {
			case 'error':
				return 'circle-close-outline';
			case 'warning':
				return 'exclamation-circle-outline';
			case 'success':
				return 'check-circle-outline';
			default:
				return 'info-circle-outline';
		}
	};
	const getStatus = type => {
		switch (type) {
			case 'info':
				return 'info';
			case 'error':
				return 'critical';
			case 'warning':
				return 'warning';
			case 'success':
				return 'positive';
			default:
				return 'info';
		}
	};
	const hasHtml = message => {
		const node = document.createElement('div');
		node.innerHTML = message;
		return (
			Array.from(node.childNodes).findIndex(child => child.nodeType === 1) !==
			-1
		);
	};
	const isHtml = hasHtml(message);
	const content = {
		type: isHtml ? 'html' : 'string',
		value: isHtml ? `<div>${message}</div>` : message
	};

	return {
		id,
		status: getStatus(type),
		icon: getIcon(type),
		content,
		action: {
			type: 'dismiss'
		}
	};
};

const generateId = (prefix, componentId, notificationId) => {
	let namespace = prefix || componentId || 'generated_';
	if (!notificationId) return uniqueId(namespace);
	return `${namespace}_${notificationId}`;
};

const addNotificationsEffect = ({ state, action, dispatch, updateState }) => {
	const {
		properties: {
			propagateNotifications = false,
			interceptNotifications = false
		} = {}
	} = state;
	const {
		meta: { id: componentId },
		payload: { prefix, items = [] }
	} = action;
	if (interceptNotifications || !(Array.isArray(items) && items.length)) return;
	if (propagateNotifications) {
		dispatch(actions.SET_PROPAGATED_NOTIFICATIONS, {
			type: 'ADD',
			prefix: componentId,
			items
		});
	} else {
		updateState(({ state }) => {
			const newNotifications = items.map(notification => {
				if (notification.type && notification.message) {
					const { id, type, message } = notification;
					return transformedNotification(
						generateId(prefix, componentId, id),
						type,
						message
					);
				} else {
					return {
						...notification,
						id: generateId(prefix, componentId, notification.id)
					};
				}
			});
			const notifications = sortNotifications(
				uniqBy([...newNotifications, ...state.notifications], 'id')
			);
			dispatch(actions.NOTIFICATIONS_UPDATED, {
				notifications: notifications
			});
			return {
				path: 'notifications',
				value: notifications,
				operation: 'set'
			};
		});
	}
};

const removeNotificationsEffect = ({
	state,
	action,
	dispatch,
	updateState
}) => {
	const {
		properties: {
			propagateNotifications = false,
			interceptNotifications = false
		} = {}
	} = state;
	const {
		meta: { id: componentId },
		payload: { prefix, items = [] }
	} = action;

	if (interceptNotifications || !(Array.isArray(items) && items.length)) return;
	if (propagateNotifications) {
		dispatch(actions.SET_PROPAGATED_NOTIFICATIONS, {
			type: 'REMOVE',
			prefix: componentId,
			items
		});
	} else {
		updateState(({ state }) => {
			const validIds = items.reduce((acc, curr) => {
				const { id } = curr;
				if (id) acc.push(generateId(prefix, componentId, id));
				return acc;
			}, []);
			if (validIds.length === 0) return;

			const notifications = sortNotifications(
				state.notifications.filter(
					notification => !validIds.includes(notification.id)
				)
			);

			dispatch(actions.NOTIFICATIONS_UPDATED, {
				notifications: [...notifications]
			});

			return {
				path: 'notifications',
				value: notifications,
				operation: 'set'
			};
		});
	}
};

const clearNotificationsEffect = ({ state, dispatch, updateState }) => {
	const {
		properties: {
			propagateNotifications = false,
			interceptNotifications = false
		} = {}
	} = state;
	if (interceptNotifications) return;

	if (propagateNotifications) {
		dispatch(actions.SET_PROPAGATED_NOTIFICATIONS, {
			type: 'CLEAR'
		});
	} else {
		updateState(() => {
			dispatch(actions.NOTIFICATIONS_UPDATED, { notifications: [] });
			return {
				path: 'notifications',
				value: [],
				operation: 'set'
			};
		});
	}
};

const setNotificationsEffect = ({ state, action, dispatch, updateState }) => {
	const { properties: { interceptNotifications = false } = {} } = state;
	if (interceptNotifications) return;
	updateState(({}) => {
		let {
			payload: { value = [] }
		} = action;
		const notifications = sortNotifications(value);
		dispatch(actions.NOTIFICATIONS_UPDATED, {
			notifications
		});
		return {
			path: 'notifications',
			value: notifications,
			operation: 'set'
		};
	});
};

const setPropagatedNotificationsEffect = ({
	host,
	state,
	action,
	dispatch
}) => {
	const hostId = host.getComponentId();
	const actionHostId = action.meta.id;
	if (hostId === actionHostId) return;
	const {
		properties: {
			interceptNotifications = false,
			propagateNotifications = []
		} = {}
	} = state;
	if (propagateNotifications) return;
	action.stopPropagation();
	if (interceptNotifications) return;
	const { type, prefix, items = [] } = action.payload;
	switch (type) {
		case 'ADD':
			dispatch(handlers.ADD_NOTIFICATIONS, { prefix, items });
			break;
		case 'REMOVE':
			dispatch(handlers.REMOVE_NOTIFICATIONS, { prefix, items });
			break;
		case 'CLEAR':
			dispatch(handlers.CLEAR_NOTIFICATIONS, {});
			break;
		default:
			break;
	}
};

const setLoadingStateEffect = ({ action, dispatch, updateState }) => {
	action.stopPropagation();
	updateState(({ state }) => {
		let {
			loaders_blocking: spinners = [],
			loaders_non_blocking: bars = []
		} = state;
		const new_spinners = [...spinners];
		const new_bars = [...bars];
		const {
			meta: { id: componentId },
			payload: {
				id: customId,
				loading,
				label,
				action: loadingAction,
				nonBlocking = false
			}
		} = action;
		const loaderId = customId || componentId;
		let stateUpdate;
		if (!loading) {
			let index = spinners.findIndex(loader => loader.loaderId === loaderId);
			if (index !== -1) new_spinners.splice(index, 1);
			stateUpdate = {
				path: 'loaders_blocking',
				start: index,
				deleteCount: 1,
				operation: 'splice'
			};

			index = bars.findIndex(loader => loader.loaderId === loaderId);
			if (index !== -1) {
				new_bars.splice(index, 1);
				stateUpdate = {
					path: 'loaders_non_blocking',
					start: index,
					deleteCount: 1,
					operation: 'splice'
				};
			}
		} else {
			if (!nonBlocking) {
				new_spinners.push({ loaderId, label, action: loadingAction });
				stateUpdate = {
					path: 'loaders_blocking',
					value: { loaderId, label, action: loadingAction },
					operation: 'push'
				};
			} else {
				new_bars.push({ loaderId, label, action: loadingAction });
				stateUpdate = {
					path: 'loaders_non_blocking',
					value: { loaderId, label, action: loadingAction },
					operation: 'push'
				};
			}
		}
		dispatch(actions.LOADING_STATUS_CHANGED, {
			loaders_blocking: new_spinners,
			loaders_non_blocking: new_bars
		});
		return stateUpdate;
	});
};

const view = state => {
	const {
		properties: {
			label,
			bare,
			scrollable,
			headingLevel,
			interceptNotifications,
			propagateNotifications
		},
		notifications = [],
		loaders_blocking = [],
		loaders_non_blocking = []
	} = state;
	const isLoading = (loaders_blocking.length > 0) | (loaders_non_blocking > 0);
	let totalNotifications = [];
	if (!interceptNotifications && !propagateNotifications)
		totalNotifications = notifications;
	return (
		<div
			class={{
				page: true,
				padded: !bare,
				loading: isLoading,
				[`scroll-${scrollable}`]: scrollable
			}}
		>
			{loaders_blocking.length > 0 ? (
				<div className="loading-overlay">
					<now-loader
						size="lg"
						label={loaders_blocking[0].label}
						action={loaders_blocking[0].action}
					></now-loader>
				</div>
			) : null}
			{loaders_blocking.length === 0 && loaders_non_blocking.length > 0 ? (
				<now-progress-bar pathType="initial" size="sm"></now-progress-bar>
			) : null}
			<sn-uxf-page-notifications
				className="notifications"
				notifications={totalNotifications}
			/>
			{isEmpty(label) ? null : (
				<now-heading
					className="heading"
					level={toString(headingLevel)}
					variant="header-primary"
					wontWrap={true}
					label={toString(label)}
				/>
			)}
			<slot>
				<now-empty-state variant="auto" />
			</slot>
		</div>
	);
};

const TAG_NAME = 'now-uxf-page';
createCustomElement(TAG_NAME, {
	renderer: {
		type: snabbdom,
		view
	},
	behaviors: [slottedPolyfillBehavior],
	properties: {
		label: { default: '' },
		bare: {
			default: false,
			schema: {
				type: 'boolean'
			}
		},
		scrollable: {
			default: 'y'
		},
		headingLevel: { default: 1 },
		interceptNotifications: {
			default: false,
			schema: {
				type: 'boolean'
			}
		},
		propagateNotifications: {
			default: false,
			schema: {
				type: 'boolean'
			}
		}
	},
	actionHandlers: {
		[handlers.ADD_NOTIFICATIONS]: {
			effect: addNotificationsEffect,
			stopPropagation: true
		},
		[handlers.REMOVE_NOTIFICATIONS]: {
			effect: removeNotificationsEffect,
			stopPropagation: true
		},
		[handlers.CLEAR_NOTIFICATIONS]: {
			effect: clearNotificationsEffect,
			stopPropagation: true
		},
		[handlers.SET_LOADING_STATE]: {
			effect: setLoadingStateEffect,
			stopPropagation: true
		},
		[actions.SET_PROPAGATED_NOTIFICATIONS]: {
			effect: setPropagatedNotificationsEffect
		},
		'NOW_ALERT_LIST#ITEMS_SET': {
			effect: setNotificationsEffect,
			stopPropagation: true
		},
		'NOW_ALERT#ACTION_CLICKED': {
			effect: ({ action, dispatch }) => {
				const { payload: { alertId, action: alertAction } = {} } = action;
				dispatch(actions.NOTIFICATION_ACTION_CLICKED, {
					alertId,
					action: { ...alertAction }
				});
			}
		},
		'NOW_LOADER#ACTION_CLICKED': {
			effect: ({ action, dispatch }) => {
				dispatch(actions.LOADER_ACTION_CLICKED, { ...action.payload });
			}
		},
		'PAGE_NOTIFICATIONS#RECORD_LINK_CLICKED': {
			effect: ({ action, dispatch }) => {
				dispatch(actions.RECORD_LINK_CLICKED, { ...action.payload });
			},
			stopPropagation: true
		}
	},
	initialState: {
		notifications: [],
		loaders_blocking: [],
		loaders_non_blocking: []
	},
	styles
});

export default TAG_NAME;
