/**
 * Seismic does not polyfill the `::slotted` psuedo-selector, which we use all
 * over this project to set margin between multiple slotted elements.
 *
 * This is a hacky workaround to inline margins onto the slotted components
 * directly for IE11/Edge browsers.
 */
import { toLower, forEach } from 'lodash';

const MARGIN_MAP = {
	'now-uxf-page': '1.5rem',
	default: '0.75rem'
};

export function getMargin(locals, el) {
	return (
		locals[toLower(el.tagName)] ||
		locals[toLower(el.getAttribute('parent-tag'))] ||
		locals.default
	);
}

export function isIEOrEdge(userAgent) {
	return /Trident\/|Edge/.test(userAgent);
}

export default {
	name: 'slottedPolyfill',
	onConnect(el, _, { selector = '*' }) {
		if (!isIEOrEdge(window.navigator.userAgent)) {
			return;
		}

		const margin = getMargin(MARGIN_MAP, el);

		if (margin) {
			forEach(el.querySelectorAll(selector), el => {
				el.style.display = 'block';
				el.style.marginBottom = margin;
			});
		}
	}
};
