import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-empty-state';
import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';
import styles from './styles.scss';
import { uxfViewportBehavior } from '@devsnc/library-uxf';

const { COMPONENT_CONNECTED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

function requestRoute({ dispatch, properties }) {
	const { activeRoute, viewportRoutes } = properties;

	// for now we only request the first configured route
	const route = viewportRoutes ? Object.keys(viewportRoutes)[0] : null;
	const isNewRoute = route !== get(activeRoute, ['routeType']);

	if (route && isNewRoute) {
		dispatch('VIEWPORT_ROUTE_REQUESTED', { route });
	}
}

const view = state => {
	const {
		properties: { scrollable, height, width, viewportRoutes }
	} = state;

	const style = {
		height,
		width
	};

	// uxf runtime assumes all metadata-driven viewports use slot="viewport". We
	// need to keep the default slot around for backwards compatibility and in
	// cases where this component is used programatically/outside uxf
	const hasRoutes = !isEmpty(viewportRoutes);
	const slotName = hasRoutes ? 'viewport' : undefined;

	return (
		<div
			class={{ viewport: true, [`scroll-${scrollable}`]: scrollable }}
			style={style}
		>
			<slot name={slotName}>
				<now-empty-state variant="auto" />
			</slot>
		</div>
	);
};

const TAG_NAME = 'now-uxf-viewport';
createCustomElement(TAG_NAME, {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		scrollable: {},
		height: {},
		width: {}
	},
	actionHandlers: {
		/**
		 * When this component is given a map of routes, we need to go request that
		 * one of those routes gets loaded.
		 */
		[COMPONENT_CONNECTED]: requestRoute,
		[COMPONENT_PROPERTY_CHANGED](coeffects) {
			const { name } = coeffects.action.payload;

			if (name === 'viewportRoutes') {
				requestRoute(coeffects);
			}
		}
	},
	behaviors: [uxfViewportBehavior],
	styles
});

export default TAG_NAME;
